<template>
  <div class="banner">
    <el-carousel :interval="4000" height="500px">
      <el-carousel-item v-for="(item,index) in contents" :key="index">
        <BannerMsg :item="item"></BannerMsg>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import BannerMsg from './BannerMsg.vue'
export default {
  data() {
    return {
      // contents:[
      //   {"title":"","text":"","url":"/product","more":"","imgUrl":"https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2784190114,3335274187&fm=27&gp=0.jpg"},
      //   {"title":"","text":"","url":"/about","more":"","imgUrl":"https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=16298531,2659097705&fm=27&gp=0.jpg"},
      //   {"title":"","text":"","url":"/join","more":"","imgUrl":"https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1740837612,3301745055&fm=27&gp=0.jpg"}
      // ]
    };
  },
  props:["contents"],
  components:{
    BannerMsg
  }
};
</script>

<style>
.banner .el-carousel__button {
  height: 3px;
}
</style>



