<template>
  <div class="product">
    <BannerMsg :item="item" v-for="(item,index) in contents" :key="index"></BannerMsg>
    <ItemTitle title="" smallTitle="about us"></ItemTitle>
    <div class="content container">
      <p>2015,</p>
      <p>IT</p>
      <p>IT</p>
      <p></p>
      <p></p>
      <p></p>
    </div>
    <ItemTitle title="" smallTitle="development history"></ItemTitle>
    <img :src="about" alt="" class="about">
  </div>
</template>
<script>
import BannerMsg from '../home/BannerMsg.vue'
import ItemTitle from '../home/Item_title.vue'
import backimg2 from '../../assets/img/backimg2.jpg'
import about from '../../assets/img/about.png'
export default {
  data() {
    return {
        contents:[
        { title: "", text: "", imgUrl: backimg2}
      ],about
    };
  },
  components:{
    BannerMsg,ItemTitle
  }
};
</script>
<style>
.content {
  text-align: left;
  font-size: 14px;
  margin-bottom: 80px;
}
.about {
  display: flex;
  width: 50%;
  margin: 50px auto;
}
</style>



